"use client";

import { useQuery } from "@tanstack/react-query";
import { api } from "@/lib/api";
import Link from "next/link";
import { 
  ShoppingBag, ArrowRight, Search, Zap,
  FileText, Clock, CheckCircle2, LayoutDashboard, 
  List, Activity, Package, ChevronRight, 
  ArrowUpRight, Bell, Sparkles, TrendingUp,
  Cpu, Globe
} from "lucide-react";
import { cn } from "@/lib/utils";

export default function BuyerDashboard() {
  const { data: rfqs, isLoading: rfqsLoading } = useQuery({
    queryKey: ["buyer-rfqs-dashboard"],
    queryFn: async () => {
      const res = await api.get("/rfq/buyer");
      return Array.isArray(res.data) ? res.data : [];
    }
  });

  const { data: products, isLoading: productsLoading } = useQuery({
    queryKey: ["products"],
    queryFn: async () => {
      const res = await api.get("/products");
      return Array.isArray(res.data) ? res.data : [];
    },
    retry: 1
  });

  const stats = {
    total: rfqs?.length || 0,
    accepted: rfqs?.filter((r: any) => ['ACCEPTED', 'APPROVED', 'COMPLETED'].includes(r.status?.toUpperCase())).length || 0,
    pending: rfqs?.filter((r: any) => ['PENDING', 'WAITING', 'NEGOTIATING'].includes(r.status?.toUpperCase())).length || 0,
  };

  if (rfqsLoading || productsLoading) return <LoadingState />;

  return (
    <div className="min-h-screen bg-[#020617] text-slate-100 selection:bg-blue-500/30">
      {/* --- NEON MESH BACKGROUND GRADIENT --- */}
      <div className="fixed inset-0 overflow-hidden pointer-events-none">
        <div className="absolute -top-[10%] -left-[10%] w-[40%] h-[40%] bg-blue-600/10 blur-[120px] rounded-full" />
        <div className="absolute top-[20%] -right-[10%] w-[30%] h-[30%] bg-emerald-600/5 blur-[120px] rounded-full" />
      </div>

      {/* --- NAV BAR --- */}
      <nav className="bg-[#020617]/80 backdrop-blur-md border-b border-white/5 sticky top-0 z-50 px-8 py-4">
        <div className="max-w-7xl mx-auto flex justify-between items-center">
          <div className="flex items-center gap-10">
            <div className="flex items-center gap-3">
              <div className="bg-blue-600 p-2 rounded-lg shadow-[0_0_20px_rgba(37,99,235,0.4)]">
                <Cpu size={20} className="text-white" />
              </div>
              <span className="text-xl font-black tracking-tighter uppercase italic">
                Procure<span className="text-blue-500 not-italic">OS</span>
              </span>
            </div>
          </div>

          <div className="flex items-center gap-6">
            <div className="hidden lg:flex items-center bg-white/5 rounded-full px-4 py-2 border border-white/10">
              <Search size={14} className="text-slate-500" />
              <input type="text" placeholder="Terminal Search..." className="bg-transparent border-none text-[10px] font-bold uppercase tracking-widest focus:ring-0 w-48 placeholder:text-slate-600" />
            </div>
            <div className="w-10 h-10 rounded-full border-2 border-blue-500/50 p-0.5">
               <div className="w-full h-full bg-slate-800 rounded-full flex items-center justify-center">
                  <Globe size={16} className="text-blue-400" />
               </div>
            </div>
          </div>
        </div>
      </nav>

      <main className="max-w-7xl mx-auto px-8 py-12 relative z-10 space-y-12">
        {/* --- HEADER --- */}
        <header className="flex flex-col md:flex-row md:items-center justify-between gap-8">
          <div>
            <h2 className="text-5xl font-black tracking-[ -0.05em] text-white italic">
              COMMAND <span className="text-blue-500 not-italic">CENTER</span>
            </h2>
            <div className="flex items-center gap-3 mt-2">
               <span className="w-2 h-2 bg-emerald-500 rounded-full animate-pulse" />
               <p className="text-slate-500 font-bold uppercase text-[10px] tracking-[0.3em]">System Status: Operational / Network Active</p>
            </div>
          </div>
          <Link href="/buyer/products" className="group bg-blue-600 hover:bg-blue-500 text-white px-10 py-5 rounded-2xl font-black text-xs uppercase tracking-[0.2em] flex items-center gap-3 shadow-[0_0_30px_rgba(37,99,235,0.3)] transition-all active:scale-95">
            <Sparkles size={18} /> Market Access <ArrowRight size={18} />
          </Link>
        </header>

        {/* --- STATS --- */}
        <section className="grid grid-cols-1 md:grid-cols-3 gap-8">
          <StatCard label="Pipeline" value={stats.total} trend="+12.5%" icon={Activity} color="blue" />
          <StatCard label="Finalized" value={stats.accepted} trend="+8.2%" icon={CheckCircle2} color="emerald" />
          <StatCard label="Active Negotiation" value={stats.pending} trend="STABLE" icon={Clock} color="slate" />
        </section>

        <div className="grid grid-cols-1 lg:grid-cols-12 gap-12">
          {/* --- INVENTORY --- */}
          <section className="lg:col-span-8 space-y-8">
            <div className="flex items-center justify-between border-l-4 border-blue-600 pl-6">
              <h3 className="text-2xl font-black tracking-tighter uppercase italic text-white">Top Allocation</h3>
              <Link href="/buyer/products" className="text-[10px] font-black uppercase tracking-[0.2em] text-blue-400 hover:text-white transition-colors">View All Assets</Link>
            </div>

            <div className="grid grid-cols-1 sm:grid-cols-2 gap-8">
              {products.slice(0, 4).map((product: any) => (
                <ProductCard key={product._id} product={product} />
              ))}
            </div>
          </section>

          {/* --- SIDEBAR --- */}
          <aside className="lg:col-span-4 space-y-8">
            <div className="bg-gradient-to-br from-slate-900 to-black rounded-[2.5rem] p-10 border border-white/10 relative overflow-hidden group shadow-2xl">
               <div className="relative z-10">
                  <h4 className="text-xs font-black uppercase tracking-[0.3em] text-blue-400 mb-8">Executive Tasks</h4>
                  <div className="space-y-6">
                     <QuickAction icon={FileText} label="Draft RFQ" sub="Create Order" href="/buyer/rfq/create" />
                     <QuickAction icon={List} label="Archives" sub="System Logs" href="/buyer/rfq/list" />
                  </div>
               </div>
               <div className="absolute bottom-0 right-0 w-32 h-32 bg-blue-600/10 blur-3xl rounded-full" />
            </div>

            <div className="bg-slate-900/40 backdrop-blur-md rounded-[2.5rem] p-10 border border-white/5 shadow-inner">
               <div className="flex justify-between items-start mb-6">
                  <p className="text-[10px] font-black uppercase tracking-[0.3em] text-slate-500">Efficiency Index</p>
                  <TrendingUp size={16} className="text-emerald-500" />
               </div>
               <div className="text-6xl font-black tracking-tighter text-white tabular-nums">98.2</div>
               <div className="w-full bg-slate-800 h-1.5 rounded-full mt-6 overflow-hidden">
                  <div className="bg-blue-600 h-full w-[98%] shadow-[0_0_10px_#2563eb]" />
               </div>
            </div>
          </aside>
        </div>

        {/* --- TRANSACTION TABLE --- */}
        <section className="bg-slate-900/30 backdrop-blur-xl rounded-[3rem] border border-white/5 overflow-hidden shadow-2xl">
          <div className="px-12 py-10 border-b border-white/5 flex justify-between items-center bg-white/5">
            <div>
              <h3 className="text-2xl font-black tracking-tighter text-white italic uppercase">Negotiation Ledger</h3>
              <p className="text-[10px] font-bold text-slate-500 uppercase tracking-[0.3em] mt-2 flex items-center gap-2">
                <Activity size={12} className="text-blue-500" /> Live Blockchain-verified Updates
              </p>
            </div>
          </div>
          <div className="divide-y divide-white/5">
            {rfqs?.slice(0, 6).map((rfq: any) => <NegotiationRow key={rfq._id} rfq={rfq} />)}
          </div>
        </section>
      </main>
    </div>
  );
}

/* --- COMPONENTS --- */

function StatCard({ label, value, trend, icon: Icon, color }: any) {
  return (
    <div className="bg-slate-900/50 backdrop-blur-md p-10 rounded-[2.5rem] border border-white/5 hover:border-blue-500/30 transition-all group overflow-hidden relative">
      <div className="relative z-10 flex flex-col justify-between h-full">
        <div className="flex justify-between items-start">
          <div className={cn("p-3 rounded-xl", color === 'blue' ? "bg-blue-600/20 text-blue-400" : color === 'emerald' ? "bg-emerald-600/20 text-emerald-400" : "bg-slate-800 text-slate-400")}>
            <Icon size={24} />
          </div>
          <span className={cn("text-[10px] font-black tracking-widest px-3 py-1 rounded-full bg-white/5", color === 'emerald' ? "text-emerald-400" : "text-blue-400")}>
            {trend}
          </span>
        </div>
        <div className="mt-8">
          <p className="text-6xl font-black tracking-tighter text-white tabular-nums leading-none">{value}</p>
          <p className="text-[10px] font-black uppercase tracking-[0.3em] text-slate-500 mt-4">{label}</p>
        </div>
      </div>
      <div className="absolute top-0 right-0 p-4 opacity-5 group-hover:scale-110 transition-transform">
         <Icon size={120} />
      </div>
    </div>
  );
}

function ProductCard({ product }: { product: any }) {
  return (
    <div className="bg-slate-900/80 rounded-[2.5rem] p-8 border border-white/5 hover:border-blue-500/50 transition-all duration-500 group">
      <div className="aspect-video bg-black rounded-2xl mb-8 overflow-hidden relative border border-white/5">
        {product.images?.[0] ? (
          <img src={product.images[0]} className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-1000 opacity-80 group-hover:opacity-100" />
        ) : (
          <div className="w-full h-full flex items-center justify-center text-slate-800"><Package size={48} /></div>
        )}
      </div>
      <div className="space-y-2 mb-8 pr-4">
        <h4 className="font-black text-white text-xl tracking-tighter uppercase italic">{product.name}</h4>
        <p className="text-blue-400 font-black text-sm uppercase tracking-widest">${product.pricePerUnit} <span className="text-slate-600 text-[10px]">/ UNIT COST</span></p>
      </div>
      <Link href={`/buyer/rfq/create?productId=${product._id}`} className="w-full py-5 bg-white text-black rounded-2xl font-black text-[11px] uppercase tracking-[0.2em] flex items-center justify-center gap-3 hover:bg-blue-500 hover:text-white transition-all shadow-xl">
        Inquire Quote <Zap size={16} />
      </Link>
    </div>
  );
}

function NegotiationRow({ rfq }: { rfq: any }) {
  const isAccepted = rfq.status?.toUpperCase() === 'ACCEPTED';
  return (
    <div className={cn("px-12 py-8 flex flex-col md:flex-row items-center gap-10 transition-all", isAccepted ? "bg-emerald-500/5" : "hover:bg-white/5")}>
      <div className={cn("w-16 h-16 rounded-2xl flex items-center justify-center shrink-0 border", isAccepted ? "border-emerald-500/50 text-emerald-400 bg-emerald-500/10" : "border-white/10 text-slate-500 bg-white/5")}>
        {isAccepted ? <CheckCircle2 size={28} /> : <Clock size={28} />}
      </div>
      <div className="flex-1 text-center md:text-left">
        <div className="flex items-center justify-center md:justify-start gap-4 mb-2">
          <span className={cn("text-[9px] font-black px-3 py-1 rounded-full uppercase tracking-widest", isAccepted ? "bg-emerald-500 text-black" : "bg-slate-800 text-slate-400")}>
            {rfq.status}
          </span>
          <span className="text-[10px] font-bold text-slate-600 font-mono">TXN_ID: {rfq._id.slice(-8).toUpperCase()}</span>
        </div>
        <h4 className="text-2xl font-black text-white tracking-tighter uppercase italic">{rfq.productId?.name || "Asset Unit"}</h4>
      </div>
      <div className="text-right hidden lg:block pr-12">
        <p className="text-[10px] font-black text-slate-600 uppercase tracking-[0.2em] mb-1">Asset Value</p>
        <p className={cn("text-3xl font-black tracking-tighter tabular-nums", isAccepted ? "text-emerald-400" : "text-white")}>
          ${(rfq.quantity * rfq.targetUnitPrice).toLocaleString()}
        </p>
      </div>
      <Link href={isAccepted ? `/buyer/invoice/${rfq._id}` : `/buyer/rfq/${rfq._id}`} className={cn("px-10 py-5 rounded-2xl font-black text-[10px] uppercase tracking-[0.2em] flex items-center gap-3 transition-all", isAccepted ? "bg-emerald-500 text-black hover:bg-emerald-400 shadow-[0_0_20px_rgba(16,185,129,0.3)]" : "bg-white/10 text-white hover:bg-blue-600 border border-white/10 hover:border-transparent")}>
        {isAccepted ? <><FileText size={16} /> Get Invoice</> : <>Terminal <ChevronRight size={16} /></>}
      </Link>
    </div>
  );
}

function QuickAction({ icon: Icon, label, sub, href }: any) {
  return (
    <Link href={href} className="flex items-center gap-6 p-2 group">
      <div className="bg-white/5 p-4 rounded-xl group-hover:bg-blue-600 group-hover:text-white transition-all text-blue-400 border border-white/5">
        <Icon size={20} />
      </div>
      <div>
        <p className="text-sm font-black uppercase tracking-[0.1em] text-white">{label}</p>
        <p className="text-[10px] font-bold text-slate-600 uppercase">{sub}</p>
      </div>
    </Link>
  );
}

function LoadingState() {
  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-[#020617] gap-8">
      <div className="relative">
         <div className="w-20 h-20 border-[3px] border-blue-600/20 border-t-blue-500 rounded-full animate-spin" />
         <Cpu className="absolute inset-0 m-auto text-blue-500 animate-pulse" size={32} />
      </div>
      <div className="text-center">
        <p className="text-[10px] font-black uppercase tracking-[0.5em] text-blue-500 mb-2">Initializing OS</p>
        <p className="text-[9px] font-bold uppercase tracking-[0.2em] text-slate-700">Establishing Secure Uplink...</p>
      </div>
    </div>
  );
}